import { useState } from "react"

function HomePage() {
    const [state, setState] = useState();

    return <div id="home">
        <form>
        <label>당신의 별명을 입력하새오</label>
        <input type="text" value="state" required />
        <input type="submit" />
        </form>
    </div>
  }
  
  export default HomePage